import './styles/App.scss';
import {Appbar, AssessmentCheckModal} from './components';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation
} from "react-router-dom";
import routes from "./router";
import {
  TransitionGroup,
  CSSTransition
} from "react-transition-group";
import ProtectedRoute from './routes'
import {useDispatch, useSelector} from "react-redux";

const _AnimatedApp = () => {
  const location = useLocation();
  return (
    <TransitionGroup>
      <CSSTransition
        key={location.key}
        classNames="fade"
        timeout={750}
      >
        <Switch>
          {routes.map((route, index) => (
            route.protected ? <ProtectedRoute key={index} {...route} /> : <Route key={index} {...route} />
          ))}
        </Switch>
      </CSSTransition>
    </TransitionGroup>
  );
}

function App() {
  const show = useSelector(state => state.assessment.showAssessmentCheckModal);
  const dispatch = useDispatch();
  return (
    <div className="App">
      <Router>
        <Appbar />
        <_AnimatedApp />
        <AssessmentCheckModal show={show} onClose={() => dispatch({type: 'closeAssessmentCheckModal'})} />
      </Router>
    </div>
  );
}

export default App;
