const LockedReportSection = (props) => {
    const lockedItems = [];
    for (var i = 0; i < props.count; i++) {
        lockedItems.push(<p>{props.content}</p>);
    }
    return (
        <div class="report--section">
            <p dangerouslySetInnerHTML={createHTML(props.content)} />
            <div class="report--section-locked">
                <div className="report--section-children">
                    {lockedItems}
                </div>
                <div className="report--section-overlay flex-center flex-column">
                    <p className="font-weight-bold h6">{props.subContent || "To Know more"}</p>
                    <Link 
                    // onClick={(props) => openPayment(props)}
                    to="/checkout"
                    >Unlock full report</Link>
                </div>
            </div>
        </div>
    )
}

const ReportSection = ({ title, content, isHTML, isLocked, subContent, totalCount = 3 }) => {
    return <section className="report-section p-4">
        <p className="font-weight-bold">{title}</p>
        <ul>
            {
                // If isHTML is true, always render it directly from the server via dangerouslySetInnerHTML
                // otherwise  the value should be an Array or a String Value, 
                //in that case either map or render the  content
                !isLocked ?
                    isHTML ? <div dangerouslySetInnerHTML={createHTML(content)} />
                        : Array.isArray(content) ? content.map((item, index) => <li key={index}>{item}</li>)
                            : <p>{content}</p> : <LockedReportSection count={3} content={content} subContent={subContent} />

            }
        </ul>
    </section>
}


const OverviewTab = ({ overview, qualities, strengths, weakness, isLocked }) => {
    return (<div>
        <div className="p-4">{overview.map((item, index) => <p key={index}>{item}</p>)}</div>
        <ReportSection isLocked={isLocked} title={qualities.title} content={qualities.listItems} />
        <ReportSection isLocked={isLocked} title={strengths.title} content={strengths.listItems} />
        <ReportSection isLocked={isLocked} title={weakness.title} content={weakness.listItems} />
    </div>)
}

const CareersAndWorkTab = ({ guidelinesForPotential, topFieldsOfWork, careersToAvoid, isLocked }) => {
    return (<div>
        <ReportSection isLocked={isLocked} title={guidelinesForPotential.title} content={guidelinesForPotential.listItems} isHTML={true} />
        <ReportSection isLocked={isLocked} title={topFieldsOfWork.title} content={topFieldsOfWork.value} />
        <ReportSection isLocked={isLocked} title={careersToAvoid.title} content={careersToAvoid.text.split(',')} />
    </div>)
}

const AboutYouTab = ({ impression, interestingFacts, inATeam, atWork, asLeader, hobbies, isLocked }) => {
    return (<div>
        <ReportSection isLocked={isLocked} title={impression.title} content={impression.paragraphs} />
        <ReportSection isLocked={isLocked} title={hobbies.title} content={hobbies.text} />
        <ReportSection isLocked={isLocked} title={interestingFacts.title} content={interestingFacts.listItems} />
        <ReportSection isLocked={isLocked} title={inATeam.title} content={inATeam.listItems} />
        <ReportSection isLocked={isLocked} title={atWork.title} content={atWork.listItems} />
        <ReportSection isLocked={isLocked} title={asLeader.title} content={asLeader.listItems} />
    </div>)
}

const ReportScore = ({ value, label, max }) => {
    return (
        <Col>
            <p className="h3 font-weight-bold text-gold">{value} {max && <small>/ {max}</small>}</p>
            <small>{label}</small>
        </Col>
    );
}

const AssessmentReport = ({ report, cqScore, traitScores, chartData, isLocked }) => {
    return (
        <Container>
            <br />
            <br />
            <div className="text-center">
                <p>{report.heading || "You are a"}</p>
                <h1 className="h2 font-weight-bold text-gold">{report.title}</h1>
                <Row>
                    <ReportScore label="Your CQ Score" value={cqScore} max="10" />
                    <ReportScore label="Courses to choose from" value="50+" />
                </Row>
                <br />
                <br />
                <style>
                </style>
                {chartData && <RadarChart
                    captions={chartData.captions}
                    data={chartData.data}
                    size={250}
                />}
                <br />
                <br />
            </div>
            <Tabs fill defaultActiveKey={'overview'} id="report-tabs">
                <Tab eventKey='overview' title='Overview'>
                    <OverviewTab
                        isLocked={isLocked}
                        overview={report.overview.paragraphs}
                        qualities={report.qualities}
                        strengths={report.strengths}
                        weakness={report.weaknesses}
                    />
                </Tab>
                <Tab eventKey='careerAndWork' title='Career & Work'>
                    <CareersAndWorkTab
                        isLocked={isLocked}
                        guidelinesForPotential={report.guidelinesToAchieveFullPotential}
                        topFieldsOfWork={report.topFieldsOfWork}
                        careersToAvoid={report.careersToAvoid}
                    />
                </Tab>
                <Tab eventKey='aboutYou' title='About You'>
                    <AboutYouTab
                        isLocked={isLocked}
                        impression={report.impressionYouMake}
                        interestingFacts={report.interestingFactsAboutYou}
                        hobbies={report.hobbies}
                        inATeam={report.inATeam}
                        atWork={report.atWork}
                        asLeader={report.asLeader}
                    />
                </Tab>
            </Tabs>
        </Container>
    )
}

const AssessmentReportLocked = ({ report }) => <AssessmentReport cqScore={report.cqScore} chartData={report.chartData} report={report.reportData} isLocked={report.reportData.isLocked} />


function mapStateToProps(state) {
    return { report: state.assessment.report }
}

export default connect(mapStateToProps, null)(AssessmentReportLocked)