import {connect} from 'react-redux';
import {Row, Col, Container, Alert} from "react-bootstrap";
import {OTPForm} from "../components/auth";
import CQAPI from "../api";
import {useState} from "react";


const VerifyPhone = (props) => {
    const [error, setError] = useState();

    const handleLogin = (value) => {
        CQAPI.signIn({userId: props.user.id, otp: value}).then((resp) => {
          props.onSuccess({
            user: resp.user,
            tokens: resp.tokens
          })
          const params = new URLSearchParams(props.history.location.search);
          const redirect = params.get('redirect') || 'home'
          console.log(redirect)
          props.history.push(`/${redirect}`)
        })
        .catch((err) => setError(err.toString()));
    }
    return (<Container>
        <Row bsPrefix="row justify-content-center align-items-center filled">
            <Col md={4}>
                {error && <Alert variant="danger">{error}</Alert>}
                <OTPForm submitText='Login' onSubmit={handleLogin}/>
            </Col>
        </Row>
    </Container>)
};

function mapStateToProps(state) {
    return {user: state.auth.user}
}

const mapDispatchToProps = dispatch => {
    return {
        onSuccess: (user) => dispatch({type: 'auth/setUser', payload: user}),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(VerifyPhone)