import { connect } from 'react-redux';
import { Row, Col, Container,  Alert } from "react-bootstrap";
import { LoginForm, SignUpForm, OTPForm } from "../components/auth";
import { Link } from "react-router-dom";
import React, {useState} from "react";
import CQAPI from "../api";



export const SignUp = (props) => {
  const [error, setError] = useState();
  // const params = new URLSearchParams(props.history.location.search);
  // const redirect = params.get('redirect') || 'home'
  const handleLogin = (user) => {
    CQAPI.signUp(user).then((resp) => {
      props.onSuccess({user:resp.user, tokens: resp.tokens})
      props.history.push(`/verify-phone?redirect=assessment-submit`)
    }).catch((err) => setError(err.toString()))
  }
  return (<Container>
    <div className="fullscreen flex-center">
      <Col sm={8} md={6} lg={6}>
        {error && <Alert variant="danger">{error}</Alert>}
        <SignUpForm onSubmit={handleLogin} />
      </Col>
    </div>
  </Container>)
};

function mapStateToProps(state) {
  return { user: state.auth.user}
}

const mapDispatchToProps = dispatch => {
  return {
    onSuccess: (user) => dispatch({ type: 'auth/setUser', payload: user }),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SignUp)